import { Grid, List, ListItem, Stack, Typography } from "@mui/material";
import { Link, Outlet } from "react-router-dom";
import HomeRoundedIcon from "@mui/icons-material/Home";
import SettingsRoundedIcon from "@mui/icons-material/SettingsRounded";
import DataUsageIcon from "@mui/icons-material/DataUsage";
import React from "react";

export default function MainLayout() {
  const [optPage, setOptPage] = React.useState<String>("");
  return (
    <Grid container spacing={2}>
      <Grid item xs={1}>
        <List sx={{ backgroundColor: "#f3f6fc", height: "100vh", pt: 2 }}>
          <ListItem key="" onClick={() => setOptPage("")}>
            <Stack
              spacing={1}
              justifyContent="center"
              alignItems="center"
              sx={{
                width: "100%",
                backgroundColor: optPage === "" ? "PowderBlue" : "default",
                borderRadius: "10%",
              }}
            >
              <Link to={"/orderPlacement"}>
                <HomeRoundedIcon />
              </Link>
              <Typography variant="button" align="center">
                
              </Typography>
            </Stack>
          </ListItem>
          <ListItem
            key=""
            onClick={() => setOptPage("")}
          >
            <Stack
              spacing={1}
              justifyContent="center"
              alignItems="center"
              sx={{
                width: "100%",
                backgroundColor:
                  optPage === "" ? "PowderBlue" : "default",
                borderRadius: "10%",
              }}
            >
              <Link to={"/settings"}>
                <SettingsRoundedIcon />
              </Link>
              <Typography variant="button" align="center">
                
              </Typography>
            </Stack>
          </ListItem>
          <ListItem key="" onClick={() => setOptPage("")}>
            <Stack
              spacing={1}
              justifyContent="center"
              alignItems="center"
              sx={{
                width: "100%",
                backgroundColor:
                  optPage === "" ? "PowderBlue" : "default",
                borderRadius: "10%",
              }}
            >
              <Link to={"/data"}>
                <DataUsageIcon />
              </Link>
              <Typography variant="button" align="center">
                
              </Typography>
            </Stack>
          </ListItem>
        </List>
      </Grid>
      <Grid item xs={11}>
        <Outlet />
      </Grid>
    </Grid>
  );
}
